.usersphoto {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 50%;
  cursor: pointer;
}
.userscont {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding-left: 10px;
  margin-top: 10px;
}
.selectedPage {
  font-weight: 700;
  color: darkgreen;
  font-size: 24px;
}
.paginate {
  cursor: pointer;
  background-color: transparent;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  text-align: center;
}
.paginatorWrapper {
  display: flex;
  margin-left: 10px;
  margin-top: 10px;
  gap: 10px;
  align-items: center;
}
.paginateButton {
  background-color: transparent;
  /* box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.15); */
  border: 2px solid;
  border-color: darkmagenta !important;
  background-color: transparent;
  transition: all 0.4s;
}
.paginateButton:hover {
  transform: scale(1.1);
}
