.wrap img {
  width: 150px;
  object-fit: contain;
}
.wrap {
  padding: 10px;
}
.usercontent {
  display: flex;
  gap: 2%;
  border: 3px solid purple;
  border-radius: 8px;
  padding: 5px;
}
.usercontent img {
  object-fit: contain;
}
.usercontent_job {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.usercontent_job img {
  width: 40px;
}
.lookingforjob {
  display: flex;
}
.status {
  display: flex;
  gap: 5px;
  align-items: center;
}
.status input {
  color: black;
  border: 2px solid purple;
  border-radius: 8px;
}
.status input:focus,
.status input:target {
  border-color: darkorchid;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(158, 158, 158, 0.25);
  transform: scale(1.3);
}
