.formcontrol textarea {
  background-color: pink;
  font-size: 22px;
  background-image: linear-gradient(
    157deg,
    rgba(111, 23, 109, 0.3617822128851541) 3%,
    rgba(165, 77, 196, 0.5606617647058824) 85%
  );
  border: 3px solid purple;
  border-radius: 8px;
}
.formcontrol ::placeholder {
  color: aliceblue;
  text-align: center;
  opacity: 0.7;
}
.formcontrol input {
  border-radius: 8px;
}
.formcontrol textarea.error,
.formcontrol input.error {
  color: red;
  border: 2px solid red;
}
.errorspan {
  color: red;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
}
.formsummaryerror {
  padding: 5px;
  background-color: red;
  color: black;
  border: 2px solid darkred;
  border-radius: 8px;
}
